.auth__form-submit-btn {
        width: 358px;
        height: 50px;
        background: #ffffff;
        border-radius: 2px;
        font-size: 18px;
        line-height: 22px;
        text-align: center;
        color: #000000;
        margin: 216px 0 112px auto;
        padding: 0;
        box-sizing: border-box;
      }
      
      @media screen and (max-width: 510px) {
        .auth__form-submit-btn {
          width: 260px;
          height: 46px;
          font-size: 16px;
          line-height: 19px;   
          margin: 173px 0 77px auto;
        }
      
}